@media only screen and (max-width: 992px) {
    .anxmenu {
        display: inherit;
    }
    #app {
        margin-left: 0;
    }
    .navbar-fixed nav {
        width: 100%;
    }
    #canvasHolder {
        margin-left: 0;
    }
    .parallax-container {
        width: calc(120%);
        margin-left: -10%;
        height: 250px;
    }
}


